import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import {
  NavigationMenu,
  NavigationMenuItem,
  NavigationMenuLink,
  NavigationMenuList,
  NavigationMenuTrigger,
} from "@/components/ui/navigation-menu";
import {
  NavigationMenuContent,
  NavigationMenuIndicator,
} from "@radix-ui/react-navigation-menu";
import Link from "next/link";
import { Capabilities } from "./NavigationMenuItem";
import { Button } from "@/components/ui/button";
// import { div } from "@/components/ui/div";
import Solutions from "./solutions/solutions";

// Navbar

export default function Navigation() {
  return (
    <nav>
      <div className="container bg-red-200 min-h-200px flex flex-row content-center justify-between">
        <div className="logo border-solid border-4 border-indigo-500 w-1/5 flex flex-row content-center">
          <Avatar>
            <AvatarImage
              src="../UI/webstacks-brand-asset/webstacks-logo-dark.svg"
              alt="Image"
            />
            <AvatarFallback>Image</AvatarFallback>
          </Avatar>
          <h2>webstacks</h2>
        </div>
        <div className="menu w-full bg-black">
          <NavigationMenu className="w-full">
            <NavigationMenuList className="flex flex-row justify-between items-center w-full bg-red-500">
              <NavigationMenuItem>
                <NavigationMenuTrigger>
                  <Link href={"/"}>Capabilities</Link>
                </NavigationMenuTrigger>
                <NavigationMenuContent className="min-w-[20rem] w-[40rem] bg-white shadow p-4 flex gap-8">
                  <div>
                    <h4 className="font-semibold">
                      {Capabilities[0].Design.title}
                    </h4>
                    {Capabilities[0].Design.items.map((item, index) => (
                      <Link
                        href={item.link}
                        key={index}
                        className="block text-sm "
                      >
                        {item.name}
                      </Link>
                    ))}
                  </div>
                  <div>
                    <h4 className="font-semibold">
                      {Capabilities[0].Development.title}
                    </h4>
                    {Capabilities[0].Development.items.map((item, index) => (
                      <Link
                        href={item.link}
                        key={index}
                        className="block text-sm "
                      >
                        {item.name}
                      </Link>
                    ))}
                  </div>
                  <div>
                    <h4 className="font-semibold">
                      {Capabilities[0].SEO.title}
                    </h4>
                    {Capabilities[0].SEO.items.map((item, index) => (
                      <Link
                        href={item.link}
                        key={index}
                        className="block text-sm "
                      >
                        {item.name}
                      </Link>
                    ))}
                  </div>
                </NavigationMenuContent>
              </NavigationMenuItem>

              <NavigationMenuItem>
                <NavigationMenuLink >
                  <NavigationMenuTrigger>
                  <Link href={"/"}>Solutions</Link>
                </NavigationMenuTrigger>
                  <NavigationMenuContent className="min-w-[20rem] w-[40rem] bg-white shadow p-4 flex">
                    <Solutions/>
                  </NavigationMenuContent>
                </NavigationMenuLink>
              </NavigationMenuItem>
              <NavigationMenuItem>
                <NavigationMenuLink asChild>
                  <Link href="#">Client Stories</Link>
                </NavigationMenuLink>
              </NavigationMenuItem>
              <NavigationMenuItem>
                <NavigationMenuLink asChild>
                  <Link href="#">Blog</Link>
                </NavigationMenuLink>
              </NavigationMenuItem>
              <NavigationMenuItem>
                <NavigationMenuLink asChild>
                  <Link href="#">Company</Link>
                </NavigationMenuLink>
              </NavigationMenuItem>
            </NavigationMenuList>
          </NavigationMenu>
        </div>

        <div className="btn w-1/10">
          <Button>Book Intro Call</Button>
        </div>
      </div>
    </nav>
  );
}
